import { useState } from 'react';
import './App.css';
import { LightgalleryItem } from 'react-lightgallery';
import axiosInstance from './axiosInstance';

function App() {
  const [images, setImages] = useState([])
  const [processed, setProcessed] = useState(false)
  const [processing, setProcessing] = useState(false)

  const hasImages = images.length !== 0;

  const fileUploadHandler = (e) => {
    const files = Array.from(e.target.files)
    const readFiles = []
    
    setImages(files.map(file => ({ alt: file.name, src: URL.createObjectURL(file) })))

    files.forEach(file => {
      const reader = new FileReader();
        reader.addEventListener("load", () => {          
          const image = new Image();
          image.src = reader.result;

          image.onload = function() {
              const { width, height } = this;
              readFiles.push({ src: reader.result, alt: file.name, width, height });

              if(readFiles.length === files.length) {          
                setImages(readFiles)
              }
          };
        });
        reader.readAsDataURL(file);
    })

  }

const processImagesHandler = async () => {
  try{
    setProcessing(true)
    const { data } = await axiosInstance.post('/process', { images })
    setImages(data)
    setProcessed(true)
    setProcessing(false)
  } catch(e) {
    setProcessing(false)
  }
  
}

const resetHandler = () => {
    setImages([])
    setProcessed(false)  
}
  
  return (
    <main className='main'>
      {!hasImages && (
        <div className='fileUploadWrapper'>
          <div className='fileUploader'>
            <label htmlFor='fileUpload'>Upload images</label>
            <input type='file' accept='image/*' id="fileUpload" onChange={fileUploadHandler} multiple />
          </div>
        </div>
      )}
      {hasImages && !processed && <button className="button-88" onClick={processImagesHandler}>Process</button>}
      {hasImages && processed && <button className="button-88" onClick={resetHandler}>Reset</button>}
      {processing && <div class="lds-ripple"><div></div><div></div></div>}
      {!processing && 
      <section id="photos">
        {images.map(image => (
          <LightgalleryItem src={image.src} key={image.alt}>
            <img src={image.src} alt={image.alt} />
          </LightgalleryItem>
        ))}
      </section>}
    </main>
  );
}

export default App;
